<template>
  <form @submit.prevent="addMessage()">
      <div class="input-field btn-group">
       <i class="material-icons prefix">forum</i>
       <input type="text" placeholder="Your Message..." v-model="text" @focus="scroll">
      <div class="btn-group">
        <button type="submit" class="submit btn green ">Send Message
            <i class="material-icons prefix">send</i>
        </button>

        <button type="button" class="delete btn red " @click="deleteAllData()">Delete Your messages
            <i class="material-icons prefix">delete</i>
        </button>
      </div>
     </div>
  </form>
</template>

<script>

import db from '../firebase/init.js'
import util from '../utils/util'

export default { 
    props:['name' , 'content'],
    data(){
        return{
            text:null,
        }
    },
    methods:{
        scroll(){
            return util.scrollBottom(this.content);
        },
        addMessage(){

            let regEx = /^[a-z\.]\D|\d+?$/i
      
            let _obj  = {
                text:this.text,
                name:this.name,
                createdAt:util.getNow()
            }       
            if(this.text && regEx.test(this.text)){
                db.collection('chats').add(_obj);
                this.scroll();
                this.text = '';
            }
            else
                util.Handler('Invalid Value:Field must not be empty , and can`t start and end with space!');
            
        },
        deleteAllData(){

          let delete_message = db.collection('chats').where('name','==', this.name);
     

          delete_message.get().then(function(querySnapshot) {
              
            querySnapshot.forEach(function(doc) {
                doc.ref.delete();
                 return util.Handler('Your messages was deleted !' , false);
            });

          });
            
        },


    },



    
}
</script>

<style lang="scss">
::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background:#d1c4e9; 
}
.btn-group{
    flex-wrap: wrap;
    display: flex;
    min-width: 100%;
    margin-top: 0.5rem;
    .submit{flex-basis: 70%;}
    .delete{flex-basis: 30%;}
    .submit , .delete{
         position: relative;
        .prefix{top: 0; font-size: 1.5rem;}
    }
}

</style>



