'use strict'
import data from './Date'

/**  @private */
function _create(elem , name){
    let _elem = document.createElement(elem);
    name ? _elem.className = name : _elem;

    return _elem;
}

/**  @private */
function _select(elem){
    return document.querySelector(elem);
}

/**  @private */
function _undefined(elem){
    return elem == undefined;
}

/**  @private */
function _parseDate(data , date){
    for(let k in  data)
        if(Number(k) == date)
            return date = data[k];    
   
}

const util = () => {
    let date = new Date();
    return{
        Handler:(text , error = true , durotation = 2500) => {
            
            let content = (_select('.error') ||  _select('.success')) ? 
                          content : 
                         _create('div' , error ? 'error' : 'success' );
            
           if(!_undefined(content)){

                content.textContent = text ;
                
                let elem = _select('input[type="text"]');
                elem.insertAdjacentElement('afterend', content);

                return  setTimeout(() => content.remove() , durotation)
           }
        },
        scrollBottom(that){
            if(!_undefined(that) && !_undefined(that.scrollHeight) && !_undefined(that.clientHeight) )
                return  that.scrollTop = that.scrollHeight;
        },
        setCookie(name){
            
            date.setTime(date.getTime() + 24*60*60*10);
            document.cookie = `name=${name};path=/;expires=${date.toGMTString()}`;
        },
        validate(value){      
            let regExp = /^([a-z]{4,16})$/i; 
            if(!regExp.test(value)){
                this.Handler('Inavlid name. Name can contain only letters with min 4 and  max 16 length' , true , 1500);
                return false;
            }
            else 
                return true;
        },
        getFullDate(){

               let  days    = date.getDate(),
                    weekDay = date.getDay(),
                    months  = date.getMonth() ,
                    years   = date.getFullYear();

            months  = _parseDate(data.MOUNTS , months);
            weekDay = _parseDate(data.MOUNTS_DAY , weekDay);

            let fullDate = `${weekDay} ${days} ${months} ${years}`;

            return fullDate;
            
        },
        getNow(){

            let date    = new Date(),
                hours   = date.getHours(),
                minutes = date.getMinutes(),
                seconds = date.getSeconds();
                
                hours   < 10 ? '0' + hours : hours;
                minutes < 10 ? '0' + minutes : minutes;
                seconds < 10 ? '0' + seconds : seconds;
    
                
            let time = `${hours}:${minutes < 10 ? '0' + minutes : minutes}:${seconds < 10 ? '0' + seconds : seconds}
                        ${hours > 12 ? 'PM' : 'AM'}`;
                

            return {
                time:time,
                date:this.getFullDate()
            }
        },
        insertTo(to , first  , last){
            to.appendChild(first);
            to.appendChild(last);
        },
     

    }
}
  
export default util();


